@use "global" as *;

.portfolio-index {
  background-color: $bg;
}

.palette__inner {
  font-family: $second-font;
  @include mq(sp) {
    padding: rem(10);
    background: $white;
  }
}

.palette__select-area {
  margin-block: rem(80);
  padding: rem(60) rem(48);
  border: 0.5px solid #000;
  background: #fff;
  box-shadow: 4px 4px 4px 0px rgba(51, 51, 51, 0.25);

  @include mq(sp) {
    padding: rem(50) rem(16);
    margin-block: 0;
  }
}

.palette__type--title {
  font-weight: $normal;
  font-size: rem(32);
}

.palette__type--lists {
  position: relative;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  margin-top: rem(20);
  text-align: center;
  gap: rem(30); // 必要か確認

  @include mq(sp) {
    grid-template-columns: repeat(3, 1fr);
    gap: rem(2);
    margin-top: rem(10);
  }

  &::after {
    content: "";
    position: absolute;
    bottom: rem(-16);
    left: 50%;
    transform: translate(-50%, -50%);
    width: 85%;
    height: 0.5px;
    background: $main;

    @include mq(sp) {
      bottom: rem(-10);
      width: 95%;
    }
  }
}

.palette__type--list {
  padding: rem(4) rem(16);
  width: 100%;
  height: rem(40);
  display: grid;
  place-content: center;
  transition: all 0.3s ease 0s;

  &:hover {
    background: #888;
    color: $white;
  }
}

.palette__type--list.palette-selected {
  background: $main;
  color: $white;

  &:hover {
    opacity: 0.7;
  }
}

.palette__style--title {
  font-weight: $normal;
  margin-top: rem(40);
}

.palette__style--lists {
  position: relative;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  margin-top: rem(20);
  text-align: center;
  gap: rem(30); // 必要か確認

  @include mq(sp) {
    grid-template-columns: repeat(3, 1fr);
    gap: rem(2);
    margin-top: rem(10);
  }

  &::after {
    content: "";
    position: absolute;
    bottom: rem(-16);
    left: 50%;
    transform: translate(-50%, -50%);
    width: 85%;
    height: 0.5px;
    background: $main;

    @include mq(sp) {
      bottom: rem(-10);
      width: 95%;
    }
  }
}

.palette__style--list {
  padding: rem(4) rem(16);
  width: 100%;
  height: rem(40);
  display: grid;
  place-content: center;
  transition: all 0.3s ease 0s;

  &:hover {
    background: #888;
    color: $white;
  }
}

.palette__style--list.palette-selected {
  background: $main;
  color: $white;

  &:hover {
    opacity: 0.7;
  }
}

.palette__color--title {
  font-weight: $normal;
  margin-top: rem(40);
}

.palette__color--lists {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  margin-top: rem(20);

  @include mq(sp) {
    grid-template-columns: repeat(6, 1fr);
    gap: rem(15);
  }

  li {
    display: grid;
    place-content: center;
  }
}

.palette__color--list {
  width: rem(50);
  height: rem(50);
  border-radius: 50px;
  transition: opacity 0.3s ease 0s;

  @include mq(sp) {
    width: rem(40);
    height: rem(40);
  }

  &:hover {
    opacity: 0.7;
    border: 2px solid #333;
  }
}

.palette-red {
  background: #f00;
}

.palette-orange {
  background: #f90;
}
.palette-yellow {
  background: #ff0;
}
.palette-green {
  background: #008000;
}
.palette-blue {
  background: #00f;
}
.palette-purple {
  background: #90c;
}
.palette-pink {
  background: #ff69b4;
}
.palette-brown {
  background: #8b4513;
}
.palette-black {
  background: #333;
}
.palette-gray {
  background: #a5a5a5;
}
.palette-white {
  background: #fff;
  border: #333 solid 1px;
}
.palette-other {
  background: url("../img/palette-other-v2.webp") no-repeat center center / cover;
  border: #333 solid 1px;
}

.palette__search-btn {
  margin-top: rem(60);
}
