@use "global" as *;

.more-button a,
.search-button button,
.alllist-button button,
.send-button button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: $second-font;
  margin: 0 auto;
  padding: rem(6) rem(44);
  font-size: rem(32);
  background: #fff;
  border: 0.5px solid #333;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  text-decoration: none;
  min-width: rem(250);
  min-height: rem(68);
  text-align: center;
  outline: none;
  transition: ease 0.2s;
  white-space: nowrap;

  @include mq(sp) {
    font-size: rem(24);
    min-width: rem(200);
    line-height: 1.4;
  }
}

.alllist-button.type-2 button {
  border: none;
  padding: rem(6) rem(80);
  background: #D9D9D9;
  color: #333;

  @include mq(sp) {
    padding: rem(6) rem(64);
    font-size: rem(24);
    max-width: rem(200);
    line-height: 1.4;
  }
}

.more-button a span,
.search-button button span,
.alllist-button button span {
  position: relative;
  z-index: 3;
  color: #333;
}

.send-button button span {
  position: relative;
  z-index: 3;
  color: rgba(51, 51, 51, 0.50);
}

.more-button a:hover span,
.search-button button:hover span,
.send-button button:hover span {
  color: #fff;
}

.alllist-button a:hover span {
  color: #D9D9D9;
}

.bgskew::before {
  content: "";
  position: absolute;
  top: 0;
  left: -130%;
  background: #666;
  width: 120%;
  height: 100%;
  transform: skewX(-25deg);
}

.bgskew:hover::before {
  animation: skewanime 0.5s forwards;
}

@keyframes skewanime {
  100% {
    left: -10%;
  }
}

