.search-button button {
  padding: rem(10) rem(30);
  line-height: normal;
  width: 100%;
  max-width: rem(160);
  font-size: rem(24);

  @include mq(sp) {
    max-width: rem(155);
  }

  &:disabled {
    color: $main;
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.send-button button {
  padding: rem(10) rem(30);
  line-height: normal;
  width: 100%;
  max-width: rem(250);
  box-sizing: border-box;

  @include mq(sp) {
    max-width: rem(200);
    min-height: rem(48);
    padding: 0 rem(30);
  }

  &:disabled {
    color: $main;
    opacity: 0.5;
    cursor: not-allowed;
  }
}

.search-button button:disabled::before,
.send-button button:disabled::before {
  display: none;
}
.search-button button:disabled:hover,
.send-button button:disabled:hover {
  color: $main;
  opacity: 0.5;
  // cursor: not-allowed;
}

.search-button button:disabled:hover span {
  color: $main;
}
